.input {
  --padding-start: 20px !important;
  --padding-end: 20px !important;
  --padding-top: 16px !important;
  --padding-bottom: 16px !important;
  border: 1px solid #ccc;
  border-radius: var(--dw-border-radius-8);
}

.saveButton {
  --border-radius: var(--dw-border-radius-12);
  --box-shadow: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 52px;
  margin-bottom: 20px;
  padding: 0 20px;
}
