.header {
  color: var(--ion-text-color) !important;
  font-weight: bold;
  font-size: 12px;
  padding: 0px;
  margin: 0 20px 0px 20px;
}

.headerIosFont {
  font-size: 14px !important;
}

.item {
  border: 1px solid #ccc;
  border-radius: var(--dw-border-radius-8);
  margin-bottom: 10px;
  padding: 10px 16px 10px 16px;
}

.addButton {
  --border-radius: var(--dw-border-radius-12);
  --box-shadow: none;
  height: 48px;
}

.deleteButton {
  /* 버튼의 크기를 줄이기 위해서 패딩(padding)을 조절한다. */
  --padding-start: 1px;
  --padding-end: 1px;
  --padding-top: 1px;
  --padding-bottom: 1px;
  --box-shadow: none;
  position: relative;
  top: 14px;
  width: 18px;
  height: 18px;
}
